<template>
   <div id='songlist'  v-if="show">
       <header>
           <span class="iconfont back" @click="back">&#xe79b;</span>
            <h3 class="songname">{{this.$store.state.songName}}</h3>
            <h4>--{{this.$store.state.singerName}}--</h4>
           <img src="https://y.gtimg.cn/music/common/upload/t_playsong_ad/1207759.png?max_age=2592000" alt="">
       </header>
        <div class="bg">
            <img :src="this.$store.state.albumImgUrl" alt="">
        </div>
        <div class="wrap">
            <transition>
                <ul ref="ul">
                    <li v-for="(item,index) in this.$store.state.LrcArr" :key="index" ref="litext" >
                        {{item.text}}
                    </li>
                </ul>
            </transition>
        </div>
   </div>
</template>

<script>
import { setTimeout } from 'timers';
   export default {
       name:'',
       data(){
            return{
               id:Number,
               data:{},
               show:true,
               rotate:0,
               frameId:0,
               bol:false,
               len:0,
            }
       },
       created(){
            var arr=this.$store.state.LrcArr;
            var _this=this;
            var audio=document.getElementById("audio")
            var $len=0;
            var miniPlayer=document.getElementById("miniPlayer")
            miniPlayer.style.display="none"
            cancelAnimationFrame(this.frameId)
            function frame(){
                var duration=audio.duration
                var currentTime=audio.currentTime
                
                for(let i=_this.len;i<arr.length;i++){
                    if(currentTime>arr[i].time){
                        // console.log(i)
                        _this.len=i
                        _this.$refs.ul.style.top= -41*i+"px"
                        _this.$refs.litext[i].style.color= "#31c27c"
                        _this.$refs.litext[i].style.fontSize= "20px"
                        _this.$refs.litext[i].style.fontWeight= "700"
                        setTimeout(() => {
                            _this.$refs.litext[i].style.color= "rgb(112,112,112)"
                            _this.$refs.litext[i].style.fontSize= "16px"
                            _this.$refs.litext[i].style.fontWeight= "normal"
                        }, 500);
                    }
                }
                _this.frameId=requestAnimationFrame(frame);
            }
                
            
            setTimeout(()=>{
                frame()
            },250)
            
       },
       computed:{
          
       },
       watch:{
           
       },
       methods:{
           back(){
              this.$router.go(-1)
           },
           isActive(item){
               if(item.text==this.$store.state.nowText){
                   return true
               }else{
                   return false
               }
           }
       },
       updated(){
           
       },
       beforeDestroy(){
            var miniPlayer=document.getElementById("miniPlayer")
            miniPlayer.style.display="block"
       }
   }
</script>

<style scoped>
    .wrap{
        margin-top: 40px;
        width: 100%;
        height: 250px;
        position: relative;
        /* background: rgb(129, 129, 129); */
        overflow: hidden;
        font-weight: normal
    }
    ul{
        padding-top:120px;
        width: 100%;
        top: 0;
        left: 0;
        position: absolute;
        /* padding-top: 50px; */
        height: 300px;
        transition: top 0.5s;
        /* transition: color 0.5s */
    }
    li{
        width: 100%;
        padding:10px;
        text-align: center;
        color: rgb(112, 112, 112);
        box-sizing: border-box;
    }
    .songList{
        width: 100%;
        padding-bottom: 70px;
        background: #f8f8f8;


    }
    .songname{
        padding: 10px;
        display: inline-block;
    }

    #songlist{
        width: 100%;
        background: #f8f8f8;
        height: 100vh;
    }
    header{
        width: 100%;
        height: 80px;
        background: #f8f8f8;
        position: fixed;
        text-align: center;
    }
    header img{
        position: absolute;
        top: 0;
        padding: 10px 0;
        /* transform: translateY(-50%); */
        right: 15px;
        width: 30px;
    }
    .info{
        text-align: center;
    }
    .back{
        line-height: 50px;
        top: 0;
        left: 0;
        position: absolute;
        padding: 0px 10px;
        color: rgba(0, 0, 0, 0.315);
        font-weight: 700;
        font-size: 30px;
        letter-spacing: 2px;
    }
    .bg{
        width: 100%;
        padding-top: 80px;
        text-align: center;
    }
    .bg img{
        width: 50%;
        border-radius: 50%;
        box-shadow: 0 7px 13px rgba(0,0,0,.21);
    }
 
</style>